// Import the generated gRPC-Web client stubs and message classes
import { ItemServiceClient } from './generated/item_grpc_web_pb';
import { Empty } from 'google-protobuf/google/protobuf/empty_pb';
import { Matrix, RowItem } from './generated/item_pb';
import './css/style.css';
import backgroundImage from './css/images/background/background_game.jpg';
import callBoard from './match3-game/index.js';

document.body.style.backgroundImage = `url(${backgroundImage})`;
const clientItem = new ItemServiceClient('http://localhost:8080')
function generateMatrix() {
  const emp = new Empty(); // Gửi yêu cầu với Empty message
  return new Promise((resolve, reject) => {
    clientItem.generateMatrix(emp, {}, (err, response) => {
      if (err) {
        reject();
      } else {
        resolve(response.toObject().rowItemList);
      }
    })
  })
}

document.addEventListener('DOMContentLoaded', () => {
  generateMatrix().then((result) => {
    let itemList = result.map((row) => {
      return row.itemList;
    })
    callBoard(itemList);
    console.log(result);
  }).catch((err) => {
    console.log(err);
  });
});
